#overlay{
  height: 100vh;
  width: 100vw;
  z-index: 1;
  position: fixed;
  pointer-events: none;
}

#overlay > img {
  width: 10vh;
}

.rotatingImage {
  animation: rotate;
  animation-iteration-count: infinite;
  animation-duration: 10s;
  animation-timing-function: linear;
  z-index: 1;
  height: 100%;

}

.flowerContainer {
  width:10vh;
  height:10vh;
  position: relative;
}

.hoja {
  width:100%;
  position: absolute;
  top: 0;
  left: 0;
}

.left {
  top: 0%;
  left: 50%;
  rotate: -25deg;
}

.down {
  top: 75%;
  left: -25%;
  rotate: 90deg;
}

@keyframes rotate {
  0% {
    rotate: 0deg;
  }
  100% {
    rotate: 360deg;
  }
}
