
#animationContainer {
  width: -webkit-fill-available;
  height: 10vh;
  position: relative;
}
#overflowContainer > img {
  height: 100%;
}

@media screen and ((min-width: 1224px))  {
  #animationContainer {
  width: -webkit-fill-available;
  height: auto;
  position: relative;
  }
}


#hearts{
  animation: growingHeart;
  animation-duration: 5s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  animation-delay: 5s;
  position: absolute;
  height: 0;
  bottom: 0;
}

#heartContainer {
  z-index: -1;
  width: -webkit-fill-available;
  height: 100%;
  position: absolute;
  display: flex;
  justify-content: center;
}

#heartsLoop{
  animation: heartLoop;
  animation-duration: 10s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  position: absolute;
  bottom: 0;
}

.betweenSpace{
  width: 0vw;
  animation: shrinkSpace;
  animation-duration: 5s;
  animation-timing-function: linear;
}

#overflowContainer{
  height: inherit;
  width: -webkit-fill-available;
  display: inline-flex;
  justify-content: center;
}

#left{
  transform:rotateY(180deg);
}

@keyframes shrinkSpace {
  0%{
    width: 100%;
  }
  100%{
    width: 0%;
  }
}

@keyframes growingHeart {
  0%{
    height: 0%;
  }
  100% {
    height: 25%;
  }
}

@keyframes heartLoop {
  0%{
    height: 25%;
  }
  50% {
    height: 50%;
  }
  100% {
    height: 25%;
  }
}