.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

body {
  background-color: #e8fcea;
}

h1 {
  color: #664be4;
  font-family: 'Elsie Swash Caps';
  font-size: calc(10px + 3vmin);
}

h2 {
  font-family: 'Elsie Swash Caps';
  font-size: calc(8px + 2vmin);
}

hr {
  color:aquamarine
}

p {
  font-size: calc(9px + 1vmin);
}

button {
  background-color: #76aaa6;
  width: fit-content;
  padding: 2vh;
  border: transparent;
  color: #e8fcea;
  font-size: calc(7px + 1vmin);

}

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}

.App-link {
  color: #61dafb;
}

.desktop-style{
  width:50vw;
  height:50vh;
}

.mobile-style{
  width:100vw;
  height:50vh;
}

.modal {
  background-color:#76aaa6;
  padding: 10px;
  display: flex;
  align-items: center;
  flex-direction:column;
}

.boldText {
  font-weight: bold;
}

.copyIcon {
  padding: 0%;
  height:calc(9px + 1vmin)
}